
  .slider {
    position: relative;
    z-index: 1;
    height: 190vh;
    overflow: hidden;
    pointer-events: none;
    background: url('../../Assets/image/Ellipse.png') no-repeat center top 0%;
  }
  .slider-item {
    --items: 10;
    --width: clamp(150px, 32vw, 420px);
    --height: clamp(200px, 42vw, 540px);
    --x: calc(var(--active) * 800%);
    --y: calc(var(--active) * -90%);
    --rot: calc(var(--active) * 180deg);
    /* --opacity: calc(var(--zIndex) / var(--items) * 3 - 2); */
    overflow: hidden;
    position: absolute;
    z-index: var(--zIndex);
    width: var(--width);
    height: var(--height);
    margin: calc(var(--height) * -0.5) 0 0 calc(var(--width) * -0.5);
    top: 50%;
    left: 50%;
    user-select: none;
    transform-origin: 0% 100%;
    /* box-shadow: 0 10px 50px 10px rgba(0, 0, 0, 0.5); */
    /* background: black; */
    pointer-events: all;
    transform: translate(var(--x), var(--y)) rotate(var(--rot));
    transition: transform 0.8s cubic-bezier(0, 0.02, 0, 1);
  }
  .slider-item .slider-box {
    position: absolute;
    z-index: 1;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    transition: opacity 0.8s cubic-bezier(0, 0.02, 0, 1);
    opacity: var(--opacity);
    font-family: 'Orelo-sw-db', serif;
  }
  .slider-item .slider-box:before {
    content: '';
    position: absolute;
    z-index: 1;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    /* background: linear-gradient(to bottom, rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0) 30%, rgba(0, 0, 0, 0) 50%, rgba(0, 0, 0, 0.5)); */
  }
  
  .slider-item img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    pointer-events: none;
    border: 1px #FF0000 solid;
    padding: 1px;
  }
  .layout {
    position: absolute;
    z-index: 0;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    pointer-events: none;
  }
  .layout:before {
    content: '';
    position: absolute;
    z-index: 1;
    top: 0;
    left: 90px;
    width: 10px;
    height: 100%;
    border: 1px solid #fff;
    border-top: none;
    border-bottom: none;
    opacity: 0.15;
  }
  .layout .box {
    position: absolute;
    bottom: 0;
    left: 30px;
    color: #fff;
    transform-origin: 0% 10%;
    transform: rotate(-90deg);
    font-size: 9px;
    line-height: 1.4;
    text-transform: uppercase;
    opacity: 0.4;
  }